var swiper = new Swiper(".mySwiper", {
	slidesPerView: 1,
	grabCursor: true,
	spaceBetween: 30,
	keyboard: {
		enabled: true,
	},
	breakpoints: {
		320: {
			slidesPerView: 1.3,
		},
		769: {
			slidesPerView: 2,
		},
	},
	navigation: {
		nextEl: ".swiper-button-next",
		prevEl: ".swiper-button-prev",
	},
});


$(function(){
	var words = [
			'users fall in love',
			'scale businesses',
			'win hearts and awards'
			], i = 0; // i for counting

		setInterval(function(){
			$('.lp-sudo-right__textchange').fadeOut(function(){ //fadeout text
				$(this).html(words[i=(i+1)%words.length]).fadeIn(); //update, count and fadeIn
			});
		}, 2500 ); //2s

	
})

